import React, { Suspense } from 'react'
import { Outlet } from 'react-router-dom'
import Sidebar from './Sidebar'
import LoginModal from './LoginModal'
import LoadScene from './LoadScene'

interface AppLayoutProps {
  darkMode: boolean
  toggleDarkMode: () => void
  showLoginModal: boolean
  setShowLoginModal: (show: boolean) => void
  isFirstVisit: boolean
}

const AppLayout: React.FC<AppLayoutProps> = ({
  darkMode,
  toggleDarkMode,
  showLoginModal,
  setShowLoginModal,
  isFirstVisit
}) => {

  // ê°„ë‹¨í•œ ë¡œë”© ìŠ¤í”¼ë„ˆ ì»´í¬ë„ŒíŠ¸
  const SimpleLoadingSpinner = () => (
    <div className="flex items-center justify-center h-64">
      <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
    </div>
  )

  return (
    <div className="flex min-h-screen bg-cream-50 dark:bg-dark-900 transition-colors duration-300">
      {/* ê³ ì •ëœ ì™¼ìª½ ì‚¬ì´ë“œë°” */}
      <Sidebar 
        darkMode={darkMode} 
        toggleDarkMode={toggleDarkMode}
        onLoginClick={() => setShowLoginModal(true)}
      />
      
      {/* ì˜¤ë¥¸ìª½ ìŠ¤í¬ë¡¤ ê°€ëŠ¥í•œ ì½˜í…ì¸  */}
      <main className="flex-1 ml-0 lg:ml-80 overflow-y-auto">
        <Suspense fallback={isFirstVisit ? <LoadScene /> : <SimpleLoadingSpinner />}>
          <Outlet />
        </Suspense>
      </main>
      
      {/* ë¡œê·¸ì¸ ëª¨ë‹¬ */}
      <LoginModal 
        isOpen={showLoginModal} 
        onClose={() => setShowLoginModal(false)} 
      />
    </div>
  )
}

export default AppLayout
